import logo from './logo.svg';
import './App.css';

// Step #1, import Statements
import React from "react";
import AppSearchAPIConnector from "@elastic/search-ui-app-search-connector";
//import { SearchProvider, Results, SearchBox } from "@elastic/react-search-ui";
import {
  PagingInfo,
  ResultsPerPage,
  Paging,
  Facet,
  SearchProvider,
  Results,
  SearchBox,
  Sorting
} from "@elastic/react-search-ui";

import { Layout } from "@elastic/react-search-ui-views";
import "@elastic/react-search-ui-views/lib/styles/styles.css";
import ResultView from "./ResultView";




// Step #2, The Connector


const connector = new AppSearchAPIConnector({
  searchKey: "search-ijei4mxb5f2ggmt3y1mmdboy",
  engineName: "english-search",
  endpointBase: "https://dev-engine.ent.asia-northeast1.gcp.cloud.es.io",
  cacheResponses: false
});



// Step #3: Configuration Options

const configurationOptions = {
  alwaysSearchOnInitialLoad: true,
  apiConnector: connector,
  searchQuery: {
    search_fields: {
      // 1. Search by subtitles
      content: {}
    },
    // 2. Results: name of the video game, its genre, publisher, scores, and platform.
    result_fields: {
      
      content: {
        raw: {}
      }
    },
    // 3. Facet by scores, genre, publisher, and platform, which we'll use to build filters later.
    facets: {
      //movie: { type: "value", size: 100 }
    }
  },
  // 4 auto
    autocompleteQuery: {
    suggestions: {
      types: {
        documents: {
          // Which fields to search for suggestions.
          fields: ["content"]
        },
        // How many suggestions appear.
        //size: 5  //will cause error
      }
    }
  },
  //5 initial state
  initialState: {
                resultsPerPage: 20,
				//sortField:""
            }
};



// Step #4, SearchProvider: The Finishing Touches.

export default function App() {
  return (
    <SearchProvider config={configurationOptions}>
      <div className="App">

		<Layout
		  //header={<SearchBox />}
		  header={<SearchBox autocompleteSuggestions={false} />}
		   
		  bodyContent={<Results titleField="movie" urlField="url"  resultView={ResultView} />}
		  //bodyContent={<Results titleField="movie" urlField="url"  />}

		  bodyHeader={
			<>
			  <PagingInfo />
			  <ResultsPerPage />
			</>
		  }
		  bodyFooter={<Paging />}
		/>
		
		
      </div>
    </SearchProvider>
  );
}



